.home {
    /* Photo by <a href="https://unsplash.com/@mischievous_penguins?utm_source=unsplash&utm_medium=referral&utm_content=creditCopyText">Casey Horner</a> on <a href="https://unsplash.com/s/photos/space?utm_source=unsplash&utm_medium=referral&utm_content=creditCopyText">Unsplash</a>
   */
	background: linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.5)), url(/src/img/bg2.jpg);
    height: 100vh;

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
}

.home_content {
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
    height: 800px;
}

.home_content h1 {
    font-size: 5.5rem;
    margin-top: 2rem;
}

.home_content h3 {
    font-size: 2rem;
}

@media (max-width: 1000px) {
    .home_content h1 {
        font-size: 4rem;
        margin-top: 2rem;
    }
}

