
.portfolio-item {
    max-width: none;
    margin: 0;
}

.portfolio-item {
    display: block;
    position: relative;
    overflow: hidden;
    background-color: transparent;
}

.portfolio-item .caption {
    display: flex;
    height: 100%;
    width: 100%;
    background-color: rgba(33, 37, 41, 0.2);
    position:absolute;
    top: 0;
    bottom: 0;
    z-index: 1;
}

.portfolio-item .caption .caption-content {
    color: #fff;
    margin: auto 2rem 2rem;
        margin-right: 2rem;
        margin-bottom: 2rem;
        margin-left: 2rem;
    text-shadow: 0px 0px 20px #000000;
}

.portfolio-item .caption .caption-content h5 {
    color: var(--primary) !important;
}

.portfolio-img {
    height: 309px;
    width: 100%;
    object-fit: cover;
}

.portfolio-item:hover .portfolio-img{
    clip-path: inset(1rem);
}

.portfolio-item:hover .caption {
    background-color: rgba(41, 136, 188, 0.5);
    clip-path: inset(1rem);
}

.portfolio-item .caption {
    background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.6) 50%);
}